import wxTitle from "../../components/wx-title.vue";
import util from "../../utils/util.js";
import api from "../../utils/api.js";
import md5 from "../../utils/md5.min.js";
export default {
	data() {
		return {
			title: "找回密码",
			navIndex: 0,
			navText:["验证码登录","密码登录"],
			phone: "",
			code:"",
			passworld:"",
			isPhone: false,
			isCode: false,
			isPassworld: false,
			isLogin:false,
			isCodeActive: false,
			codeText:"获取验证码",
			code_id:''
		};
	},
	components: {
		wxTitle
	},
	methods:{
		goblack(){
			uni.navigateBack({
				delta: 1
			});
		},
		checkNav(index){
			this.$data.navIndex = index;
			this.judgeIsSelect();
			
		},
		judgeIsSelect(){
			if(util.vailPhone(this.$data.phone)&&this.$data.code!=""&&this.$data.passworld!=""){
				this.$data.isLogin = true;
			}else{
				this.$data.isLogin = false;
			}
		},
		clickPhone(event){
			console.log(this.$data.phone);
			console.log(util.vailPhone(this.$data.phone));
			this.$data.phone =  event.target.value;
			this.$data.phone != ""?this.$data.isPhone = true:this.$data.isPhone = false;
			util.vailPhone(this.$data.phone)?this.$data.isCodeActive = true:this.$data.isCodeActive = false;
			this.judgeIsSelect();
		},
		clearPhone(){
			this.$data.phone = "";
			this.$data.isLogin = false;
			this.$data.isCodeActive = false;
			this.judgeIsSelect();
			this.$data.isPhone = false;
		},
		clickCode(){
			this.$data.code != ""?this.$data.isCode = true:this.$data.isCode = false;
			this.judgeIsSelect();
			
		},
		clearCode(){
			this.$data.code = "";
			this.$data.isLogin = false;
			this.$data.isCode = false;
		},
		clickPassworld(){
			this.$data.passworld != ""?this.$data.isPassworld = true:this.$data.isPassworld = false;
			this.judgeIsSelect();
		},
		clearPassworld(){
			this.$data.passworld = "";
			this.$data.isLogin = false;
			this.$data.isPassworld = false;
		},
		generateCode(){
			if(this.$data.isCodeActive){
				api.generateCode({
					method:"POST",
					data:{
						mobile: this.$data.phone,
						type: 1
					}
				}).then((res)=>{
					if(res.code == 0){
						uni.showToast({
							title: '短信发送成功',
							icon:'none',
							duration: 2000
						});
						this.$data.codeText = "重新获取";
						this.$data.code_id = res.data.id
					}
				})
			}
		},
		doLogin(){
			if(this.$data.phone == ""){
				util.errorTips("请输入手机号码");
				return false
			}
			if(this.$data.passworld == ""){
				util.errorTips("请输入密码");
				return false
			}
			if(this.$data.code == ""){
				util.errorTips("请输入验证码");
				return false
			}
			if(this.$data.isLogin){
				api.updatePassword({
					method:"POST",
					data:{
						mobile:this.$data.phone,
						password:this.$data.passworld,
						code:this.$data.code,
						id:this.$data.code_id
					}
				}).then((res)=>{
					if(res.code==0){
						this.$store.commit('changeReset',{number: 1});
						uni.navigateBack({
							delta: 1
						});
					}
				})
				
			}
		}
	}
}
